import React from "react";
import styled from "styled-components";
import { CommonContainer, CommonWrapper } from "../styles/common";
import { moneyComma } from "../libs/utils";
const Container = styled(CommonContainer)`
  flex: 1;
`;

const Title = styled.div``;

const Wrapper = styled(CommonWrapper)``;
const Table = styled.table`
  width: 100%;
`;

const Tbody = styled.tbody`
  overflow: scroll;
`;
const TbodyTr = styled.tr``;

const Transaction = ({ transaction, id }) => {
  const renderList = (data) => {
    /*
  {
				"contDtm" : "2020-01-29 12:24:18.830039",	// 
				"contPrice" : "10579000",					// 
				"contQty" : "0.01",							// 
				"updn" : "dn"								//    : up-, dn-
			}
    */
    return data.map((item, index) => (
      <TbodyTr key={index}>
        <td>{item.contDtm}</td>
        <td>{moneyComma(item.contPrice)}</td>
        <td style={{ color: item.updn === "up" ? "red" : "blue" }}>
          {item.contQy} BTC
        </td>
      </TbodyTr>
    ));
  };

  return (
    <Container>
      <Wrapper>
        <Title></Title>
        <Table>
          <thead>
            <tr>
              <th></th>
              <th>(KRW)</th>
              <th></th>
            </tr>
          </thead>
          {transaction && Object.keys(transaction).includes(id) && (
            <Tbody>{renderList(Object.values(transaction[id]))}</Tbody>
          )}
        </Table>
      </Wrapper>
    </Container>
  );
};

export default Transaction;
